@keyframes upriseText {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-10rem);
    transform: translateY(-10rem)
  }
  80% {
      transform: translateY(-1rem)
  }
  100% {
      opacity: 1;
      transform: translate(0)
  }
}

.title-text {
  color: #007bb2;
  font-family:roboto;
  font-style: oblique;
  font-size: 1.8vw;
  font-weight: normal;
}

.uprise {
  animation: upriseText .5s ease-out .75s;
  animation-fill-mode: backwards
}